<template>
<el-breadcrumb :separator-icon="ArrowRight" class="el-breadcrumb_cls">
    <el-breadcrumb-item class="el-breadcrumb-item">论坛</el-breadcrumb-item>
    <el-button style="margin-left:600px;margin-top:5px" @click="click">发表</el-button>
  </el-breadcrumb>
  <el-divider style="margin-bottom:0px;margin-top:0px"/>
  <div class="wrapper">
    <ul class="main-wrap">
      <li v-for="item in texts" :key="item.id" class="main-item">
        <div class="li-top">
          <a :href="`#/AdminforDetail?id=${item.id}`" class="title">
            <h3>{{item.title}}</h3>
          </a>
          <div class="source">{{item.type}}</div>
        </div>
        <div class="li-middle">
          {{item.main}}
        </div>
        <div class="li-bottom">
          <div class="liBottomLeft">
            <img class="user-icon" src="data:image/png;base64,"/>
            <span>{{item.person}}</span>
            <label>发布时间：</label>
            <span>{{item.time}}</span>
          </div>
          <div class="liBottomRight">
            <span class="read">{{item.read}}</span>阅读 ·
            <span class="comment">{{item.comment}}</span>评论
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script setup>
import { reactive } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import { L2Dwidget } from 'live2d-widget'

const texts = reactive([])
L2Dwidget.init({
  model: {
    jsonPath: 'https://cdn.jsdelivr.net/gh/wangsrGit119/wangsr-image-bucket/L2Dwidget/live2d-widget-model-hijiki/assets/hijiki.model.json'
  }
})

axios.post('http://localhost:8081/graduation-project/UserMenu/ChatRoom/GetChat.php').then(function (response) {
  const count = response.data[0]
  for (let i = 1; i <= count; i++) {
    const text = {
      id: response.data[i][0],
      title: response.data[i][1],
      type: response.data[i][2],
      main: response.data[i][3],
      time: response.data[i][4],
      person: response.data[i][5],
      read: response.data[i][6],
      comment: response.data[i][7]

    }
    texts.push(text)
  }
})

const router = useRouter()

const click = () => {
  router.push('/AdminChatPublication')
}

</script>
<style>
div{
  box-sizing: border-box;
}
a{
  text-decoration:none;
}
.main-wrap{
  list-style: none;
  text-align: left;
}
.main-item{
  border-bottom: 1px solid #dcdfe6;
  padding: 18px 24px;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 4%);
  position: relative;
  top: 0;
  left: 0;
}
.title{
  max-width: 92%;
  height: 24px;
  line-height: 24px;
  color: #303133;
  cursor: pointer;
}
.title h3{
  margin: 0;
  padding: 0;
}
.li-top{
  display: flex;
}
.source{
  height: 20px;
  padding: 0 5px;
  border: 1px solid #409eff;
  border-radius: 4px;
  color: #409eff;
  font-size: 12px;
  margin-top: 3px;
  margin-left: 10px;
}
.li-middle{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 24px;
  line-height: 24px;
  margin-bottom: 4px;
  color: #606266;
  font-size: 14px;
}
.li-bottom{
  display: flex;
  position: relative;
  left: 0;
  top: 0;
  height: 24px;
  line-height: 24px;
}
.user-icon{
  height: 20px;
  width: 20px;
  margin-right: 10px;
}
.liBottomLeft{
  display: flex;
}
.liBottomLeft span,
.liBottomLeft  label{
  height: 24px;
  display: block;
  line-height: 24px;
  margin-right: 6px;
  font-size: 14px;
  color: #303133;
}
.liBottomRight{
  position: absolute;
  right: 20px;
  line-height: 24px;
  font-size: 14px;
  color: #909399;
}
.read,.comment{
  color: #409eff;
  margin-right: 5px;
}
.el-breadcrumb-item{
    font-size: 30px;
    margin:6px;
    padding:0
}

</style>
